<template>
  <div id="baseMessage">
    <div class="body">
      <div class="title">
        <span>1<i style="opacity: .5;">/4</i></span>
        <span>
          <van-icon name="records-o" size="22" color="#1989fa;"/>&nbsp;基本信息
        </span>
      </div>
      <van-divider />
      <form>
        <div class="content">
          <div class="left">
            <div class="one">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            请问您的年龄是?
            <van-tag type="warning" v-if="message[0].bool">请输入</van-tag>
          </h3>
          <input type="text" placeholder="请输入您的年龄" v-model="message[0].data">
            </div>
            <div class="two">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            请问您的性别是?
            <van-tag type="warning" v-if="message[1].bool">请选择</van-tag>
          </h3>
          <select v-model="message[1].data" style="background-color: transparent;">
            <option value="0">男</option>
            <option value="1">女</option>
            <option value="2">保密</option>
          </select>
            </div>
          </div>
          <div class="right">
            <van-tag type="warning" v-if="fileListBool" style="position: absolute;left: 60%;top: 37%;z-index: 999;">请上传</van-tag>
            <span class="iconfont icon-bitian" style="color: #ec6661;position: absolute;right: 153px;bottom: 386px;"></span>
            <van-uploader multiple  :max-count="1" preview-size="100" v-model="fileList" style="position: relative;bottom: -20px;left: -10px;"/>
            <h3 style="position: relative;left: -3px;bottom: -25px;left: -15px;">请上传您的残疾证明</h3>
          </div>
        </div>
        <div class="three">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            请问您的残疾类型是?
            <van-tag type="warning" v-if="message[2].bool">请输入</van-tag>
          </h3>
          <input type="text" placeholder="请输入您的残疾类型" v-model="message[2].data">
        </div>
        <div class="four">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            请问您目前的月收入是?
            <van-tag type="warning" v-if="message[3].bool">请输入</van-tag>
          </h3>
          <input type="text" placeholder='如"无收入"可填"无"' v-model="message[3].data">
        </div>
        <div class="five">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            请问您目前所在的地区是?
            <van-tag type="warning" v-if="message[4].bool">请选择</van-tag>
          </h3>
          <van-field
            readonly
            clickable
            name="area"
            :value="message[4].data"
            placeholder="点击选择省市区"
            @click="showArea = true"
          />
          <van-popup v-model="showArea" position="bottom">
            <van-area
              :area-list="areaList"
              @confirm="onConfirm"
              @cancel="showArea = false"
            />
          </van-popup>
        </div>
        <div class="six">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            请问您的残疾原因是?
            <van-tag type="warning" v-if="message[5].bool">请输入</van-tag>
          </h3>
          <van-field
              v-model="message[5].data"
              rows="2"
              autosize
              type="textarea"
              maxlength="150"
              placeholder="请输入您残疾的原因"
              show-word-limit
              style="
              border: 1px solid rgba(0, 0, 0,.6);
              "
              />
        </div>
      </form>
    </div>
    <div class="tapBar">
      <div class="tapBarBox">
        <button :disabled="isdisabled" @click="skip" type="buttom">下一页</button>
      </div>
    </div>
  </div>
</template>

<script>
import { areaList } from '@vant/area-data'
export default {
  data () {
    return {
      message: [
        { data: '', bool: false },
        { data: '', bool: false },
        { data: '', bool: false },
        { data: '', bool: false },
        { data: '', bool: false },
        { data: '', bool: false }
      ],
      fileList: [],
      fileListBool: false,
      isSkip: [],
      isdisabled: false,
      timer: null,
      showArea: false,
      areaList: areaList
    }
  },
  methods: {
    skip () {
      this.isSkip = JSON.parse(localStorage.getItem('baseMessage'))
      if (this.isSkip === null) {
        this.message.forEach(item => {
          item.bool = true
        })
        this.isdisabled = true
        this.timer = setTimeout(() => {
          this.message.forEach(item => {
            item.bool = false
          })
          this.isdisabled = false
        }, 2000)
        return
      }

      if (this.message.find((item) => item.data.length === 0)) {
        this.isdisabled = true
        this.message.forEach(item => {
          if (item.data.length === 0) {
            item.bool = true
          }
        })

        this.timer = setTimeout(() => {
          this.message.forEach(item => {
            item.bool = false
          })
          this.isdisabled = false
        }, 2000)
        return
      }

      if (this.fileList.length === 0) {
        this.isdisabled = true
        this.fileListBool = true

        this.timer = setTimeout(() => {
          this.fileListBool = false
          this.isdisabled = false
        }, 2000)
        return
      }

      this.$router.push('/survey/useSituation')
    },
    onConfirm (values) {
      this.message[4].data = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join('/')
      this.showArea = false
    }
  },
  created () {
    if (localStorage.getItem('baseMessage')) this.message = JSON.parse(localStorage.getItem('baseMessage'))
    if (localStorage.getItem('fileList')) this.fileList = JSON.parse(localStorage.getItem('fileList'))
  },
  watch: {
    message: {
      deep: true,
      handler (newVal) {
        localStorage.setItem('baseMessage', JSON.stringify(newVal))
      }
    },
    fileList: {
      deep: true,
      handler (newVal) {
        localStorage.setItem('fileList', JSON.stringify(newVal))
      }
    }
  }
}
</script>

<style scoped>
form .one input,
form .two select,
form .three input,
form .four input,
form .five input{
  margin: 0 20px;
  padding: 5px 10px;
  width: 300px;
  border: 1px solid rgba(0, 0, 0,.8);
  outline: none;
  color: rgba(0, 0, 0,.8);
  margin-bottom: 15px;
}
.content{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
form .one input,
form .two select{
  width: 150px;
}
.van-cell{
  border: 1px solid rgba(0, 0, 0,.8);
  color: rgba(0, 0, 0,.8);
  margin-bottom: 15px;
}
</style>
